import { ColDef, Column, GridOptions } from 'ag-grid-community';
import { DropdownKey } from '../types/generic-table';

// Columns
export interface ITableColumnDef extends ColDef {
    excludeFromGlobalSearch?: boolean; // Set for columns that should not be globally searchable
    filterOptions?: {
        exclude?: boolean; // Prevents column from being filterable (as column/field filter)
        dropdownKeys?: DropdownKey[]; // restricts filters for this column to this list, which will selected from a dropdown

        // TEMPORARY - avoid using character property
        // Todo: CR-65045 handle this in a more generic way
        character?: string; // Provide special character (e.g. '>') for a filter
    };
    sortable?: boolean; // add sorting setting to column if it is true
}

// Grid Options
export const defaultGridOptions = <GridOptions>{
    enableCellTextSelection: true,
    suppressColumnVirtualisation: false,
    rowModelType: 'infinite',
    defaultColDef: {
        sortable: true,
        resizable: true,
        filter: false,
    },
    pagination: true,
    maxBlocksInCache: 1,
    suppressRowClickSelection: true,
    headerHeight: 48,

    // replace default ag-grid icons:
    icons: {
        // column header items
        sortAscending:
            '<i class="material-icons icon--large">arrow_drop_up</i>',
        sortDescending:
            '<i class="material-icons icon--large">arrow_drop_down</i>',

        // icon for moving columns out of the grid
        columnMoveHide: '<i class="material-icons icon--large">delete</i>',

        // paging toolbar
        first: '<i class="material-icons icon--large">first_page</i>',
        previous: '<i class="material-icons icon--large">chevron_left</i>',
        next: '<i class="material-icons icon--large">chevron_right</i>',
        last: '<i class="material-icons icon--large">last_page</i>',
    },
};

export interface ICellInfo {
    rowIndex: number;
    columnKey: Column | string;
}

export interface TableCSVData {
    [key: string]: string;
}

export interface TableCSV {
    data: TableCSVData[];
    headers: string[];
}
